import React, { useEffect, useState, useRef } from "react";
import Header from "./components/Header";
import Navigation from "./components/Navigation";
import Rides from "./components/Rides";
import { ChakraProvider } from "@chakra-ui/provider";
import axios from 'axios';

function App() {

  const [rideFilter, setRideFilter]=useState([true,false,false]);
  const [state,setState]=useState('none');
  const [city,setCity]=useState('none');
  const [optionFiltered,setOptionFiltered]=useState([]);
  const [geoFiltered, setGeoFiltered] = useState([]);
  const [data, setData] = useState([{},[]]);
  const [stateList,setStateList]=useState([]);
  const [cityList,setCityList]=useState([]);
  const [pureOptionFiltered, setPureOptionFiltered]=useState([]);
  const [filterNums,setFilterNums] = useState([null,null]);

  useEffect(() => {
    const fetchData = async () => {
      if(data[1].length===0){  
      const userData = await axios.get('https://assessment.api.vweb.app/user');
      const rideListData = await axios.get('https://assessment.api.vweb.app/rides');
      let userDataTemp=userData.data;
      let rideListDataTemp=rideListData.data;
      let list=rideListDataTemp;
      list.forEach(e => 
      {e.station_path=[e.origin_station_code,...e.station_path];
        //date format
        let rideDate = new Date(e.date);
        let m = rideDate.toLocaleString('default', { month: 'short' });
        let d = rideDate.toLocaleString('default', { day: 'numeric' });
        const getOrdinalNum = (d) => ( d + (d > 0 ? ['th', 'st', 'nd', 'rd'][(d > 3 && d < 21) || d % 10 > 3 ? 0 : d % 10] : ''));
        d = getOrdinalNum(d);
        let y = rideDate.toLocaleString('default', { year: 'numeric' });
        let t = rideDate.toLocaleString([], { hour: '2-digit', minute: '2-digit',  hourCycle:'h23'});
        let formattedDate=`${d} ${m} ${y} ${t}`;
        e.fDate=formattedDate;
        //distance
        e.distance=Math.min(...(e.station_path.map(element=>Math.abs(element-userDataTemp.station_code))));
      });
      setData([userDataTemp,list]);
    }
    }
    fetchData();
},[]);
  
useEffect(() => {

  function getData()
  {
    let list=data[1];
    setStateList([...new Set([...list.map(e => e.state)])]);
    if(state==='none')
    {
      setCityList([...new Set(list.map(e => e.city))]);
    }
    else
    {
      setCityList([...new Set((list.filter(e => e.state===state)).map(e => e.city))]);
    }
      setGeoFiltered(data[1]);
      setPureOptionFiltered(data[1]);
      setOptionFiltered(data[1]);
  };
  
  getData();


},[data]);

function setFilterOptions()
  {
    if(state==='none' && city==='none')
    {
      setCityList([...new Set(data[1].map(e => e.city))]);
      setGeoFiltered(data[1]);
      setOptionFiltered(pureOptionFiltered);
    }
    else if(state==='none' && city!=='none')
    {
      setOptionFiltered(pureOptionFiltered.filter(e => e.city === city));
      setGeoFiltered(data[1].filter(e => e.city === city));
    }
    else if(state!=='none' && city==='none')
    {
      setCityList([...new Set((data[1].filter(e => e.state===state)).map(e => e.city))]);
      setGeoFiltered(data[1].filter(e => e.state === state));
      setOptionFiltered(pureOptionFiltered.filter(e => e.state === state));
    }
    else
    {
      setCityList([...new Set((data[1].filter(e => e.state===state)).map(e => e.city))]);
      setOptionFiltered(pureOptionFiltered.filter(e => e.city === city));
      setGeoFiltered(data[1].filter(e => e.city === city));
    }  
  }

useEffect(() => {

if(data[1].length>0)
setFilterOptions();

},[state,city,pureOptionFiltered,data]);

useEffect(() => {
  let today = new Date();
  let newList=geoFiltered;

let nNewList=newList.sort(function(a,b){
  return (a.distance) - (b.distance);
});

let uNewList=newList.filter(e =>  (new Date(e.date)) > today.getTime());
  uNewList=uNewList.sort(function(a,b){
      return (a.date) - (b.date);
    });
    
    let pNewList=newList.filter(e => (new Date(e.date)) < today.getTime());
    pNewList=pNewList.sort(function(a,b){
      return (b.date) - (a.date);
    });

setFilterNums([uNewList.length,pNewList.length]);
},[geoFiltered]);



useEffect(() => {

let newList=data[1];
let today = new Date();

let nNewList=newList.sort(function(a,b){
  return (a.distance) - (b.distance);
});

let uNewList=newList.filter(e =>  (new Date(e.date)) > today.getTime());
  uNewList=uNewList.sort(function(a,b){
      return (a.date) - (b.date);
    });
    
    let pNewList=newList.filter(e => (new Date(e.date)) < today.getTime());
    pNewList=pNewList.sort(function(a,b){
      return (b.date) - (a.date);
    });
    

if(rideFilter[0])
{
  setPureOptionFiltered(nNewList);
}

if(rideFilter[1])
{
  setPureOptionFiltered(uNewList);
}

if(rideFilter[2])
{
  setPureOptionFiltered(pNewList);
}


},[rideFilter,data]);

  function handleState(stateProp){
    setState(stateProp);
    setCity('none');
  }

  function handleCity(cityProp){
    setCity(cityProp);
  }

  function handleNearest(event)
  {
    document.querySelector('#nearest').style.color='white';
    document.querySelector('#upcoming').style.color='grey';
    document.querySelector('#past').style.color='grey';
    setRideFilter([true,false,false]);
  }

  function handleUpcoming(event)
  {
    document.querySelector('#upcoming').style.color='white';
    document.querySelector('#nearest').style.color='grey';
    document.querySelector('#past').style.color='grey';
    setRideFilter([false,true,false]);
  }

  function handlePast(event)
  {
    document.querySelector('#past').style.color='white';
    document.querySelector('#nearest').style.color='grey';
    document.querySelector('#upcoming').style.color='grey';
    setRideFilter([false,false,true]);
  }

  return (
    <>
      {data[1].length>0?<Header user={data[0]}/>:null}
      {data[1].length>0?<Navigation handleState={handleState} handleCity={handleCity} stateList={stateList} cityList={cityList} handleNearest={handleNearest} handlePast={handlePast} handleUpcoming={handleUpcoming} nearestRides={rideFilter[0]} upcomingRides={rideFilter[1]} pastRides={rideFilter[2]} filterNumbers={filterNums}/>:null}
      <main>
      {data[1].length>0?<Rides list={optionFiltered}/>:null}
      </main>
    </>);
  }

export default App;
