import React from 'react';
import { GridItem, Grid, ChakraProvider, Image, Box } from '@chakra-ui/react';

const Header = ({ user }) => {
  return (
    <ChakraProvider>
    <Grid className='headerbg' gap={4}>
    <GridItem className='titleedit'>Edvora</GridItem>
    <GridItem colSpan={1} display='flex'> 
    <Box className='usernameedit'>{user.name}</Box> 
    <Image className='dp' src={user.url} alt={"dp"} /></GridItem>
    </Grid>
    </ChakraProvider>
  )
}

export default Header