import React, { useState } from 'react';
import {Grid, GridItem, ChakraProvider, Button, Popover, PopoverTrigger, Icon, PopoverContent, PopoverHeader, PopoverBody} from '@chakra-ui/react';
import { BsFilterLeft } from 'react-icons/bs';



const Navigation = ({handleState, handleCity, stateList, cityList, handleNearest, handleUpcoming, handlePast, nearestRides, upcomingRides, pastRides, filterNumbers}) => {

const [state,setState] = useState('none');
const [city,setCity] = useState('none');

function handleStateChange(){
  let stateTemp=document.getElementById("state").value;
  setState(stateTemp);
  handleState(stateTemp);
}

function handleCityChange(){
  let cityTemp=document.getElementById("city").value;
  setCity(cityTemp);
  handleCity(cityTemp);
}

  return (
    <ChakraProvider>
    <div className='navbar'>
    <Grid h='75px' templateRows='repeat(1, 1fr)' templateColumns='repeat(7, 1fr)' gap={4}>
    <GridItem marginTop='20px' fontFamily={'Inter'} rowSpan={1} colSpan={1} align='center'>
      <Button color={'white'} fontSize={20} id='nearest' className='navbarButton' variant='link' onClick={handleNearest}>
      Nearest rides
      </Button>
    </GridItem>
    <GridItem marginTop='20px' fontFamily={'Inter'} rowSpan={1} colSpan={1} align='center'>
      <Button fontSize={20} id='upcoming' className='navbarButton' variant='link' onClick={handleUpcoming}>
      Upcoming rides ({filterNumbers[0]})
      </Button>
    </GridItem>
    <GridItem marginTop='20px' rowSpan={1} colSpan={1} align='center'>
      <Button fontSize={20} fontFamily={'Inter'} id='past' className='navbarButton' variant='link' onClick={handlePast}>
      Past rides ({filterNumbers[1]})
      </Button>
    </GridItem>
    <GridItem rowSpan={1} colSpan={2} align='center'></GridItem>
    <GridItem marginTop='15px' rowSpan={1} colSpan={1} align='center'>
    <Popover>
  <PopoverTrigger>
  <Button><Icon as={BsFilterLeft}/>Filter</Button>
  </PopoverTrigger>
  <PopoverContent>
    <PopoverHeader>Filters</PopoverHeader>
    <PopoverBody>
      
      <label align='left'>Select State</label><br></br>
      <select  name="states" id="state" onChange={handleStateChange}>
      <option value='none'>none</option>
      {stateList.map((e,i) => <option key={i} value={e}>{e}</option>)}
      </select><br></br>
      <label align='left'>Select City</label><br></br>
      <select padding='20px' name="cities" id="city" onChange={handleCityChange}>
      <option value='none'>none</option>
      {cityList.map((e,i) => <option key={i} value={e}>{e}</option>)}
      </select>
    </PopoverBody>
    </PopoverContent>
</Popover>
    </GridItem>
    </Grid>
    </div>
    </ChakraProvider>
  )
}

export default Navigation