import React, {useState} from 'react';
import { ChakraProvider, HStack, Image, Badge, Container, VStack, Box, Grid, GridItem } from '@chakra-ui/react'

const Rides = ({ list }) => {
  return (
    <ChakraProvider>
    <VStack className='ridebg' padding='10' spacing={4} align='stretch'>
    {list.map((e,i) => 
    <Grid className='ridelist' key={i} padding={5} templateRows='repeat(2, 1fr)' templateColumns='repeat(9, 1fr)'>
    <GridItem rowSpan={2} colSpan={2} align="center">
    <Image className='rideimg'  src={e.map_url} alt={"Map"} />
    </GridItem>
    <GridItem rowSpan={2} colSpan={3}>
    <VStack padding={1} align="left" spacing='6px' >
        <Box paddingLeft={10} color={'white'}>Ride id : {e.id}</Box>
        <Box paddingLeft={10} color={'white'}>Origin Station : {e.origin_station_code}</Box>
        <Box paddingLeft={10} color={'white'}>station_path : [{e.station_path.map((s,i) => (i===e.station_path.length-1)?<>{s}</>:<>{s},</>)}]</Box>
        <Box paddingLeft={10} color={'white'}>Date : {e.fDate}</Box>
        <Box paddingLeft={10} color={'white'}>Distance : {e.distance}</Box>
    </VStack>
    </GridItem>
    <GridItem rowSpan={1} colSpan={2}></GridItem>
    <GridItem rowSpan={1} colSpan={2}>
    <Badge borderRadius='10' px='2' color='white' bgColor={'#262626'}>
        {e.city}
    </Badge>
    <Badge borderRadius='10' px='2' color='white' bgColor={'#262626'}>
        {e.state}
    </Badge>
    </GridItem>
    </Grid>
    )}
    </VStack>
    </ChakraProvider>
  )
}

export default Rides